<template>
	<scroll-view :scroll-y="true" @scrolltolower="gtPost" :style="{
            'height': 'calc(100vh - ' + (statusBarHeight - (-navHeight)) + 'px - 191rpx)'
        }">

		<view class="post-list">
			<view class="post-list-content">
				<view v-for="(item, index) in postData.content" class="post-list-content-item">

					<view style="height: 15rpx;"></view>

					<user-post :bodyData="item"></user-post>

				</view>

			</view>
			<view style="height: 20rpx;"></view>
			<view v-if="isEnd" style="text-align: center;color:#999;font-size: 28rpx;">
				<text>没有更多了~</text>
			</view>
			<view style="height: 10rpx;"></view>
		</view>

	</scroll-view>
</template>

<script>
export default{
    props: {
        statusBarHeight: {
            type: Number,
            default: 0
        },
        navHeight: {
            type: Number,
            default: 0
        }
    },
    data(){
        return{
            postData: {
                content: []
            },
            isEnd: false,
            isPublishing: false,
            page: 1,
            hostPostList: [
                {
                    image: 'https://file.kantboot.com/image/test1.png',
                    title: '身无彩凤双飞翼，心有灵犀一点通'
                },{
                    image: 'https://file.kantboot.com/image/test2.jpg',
                    title: '谁念西风独自凉，萧萧黄叶闭疏窗，沉思往事立残阳'
                },{
                    image: 'https://img1.gtimg.com/comic/pics/hv1/84/21/2217/144165864.jpg',
                    title: '昨夜星辰昨夜风，画楼西畔桂堂东',
                }
            ],   
            requestParam:{
				pageNumber: 1,
				sortField: "gmtCreate",
				sortOrderBy: "desc",
                range:200000000000
			},
            requestCount:0
        }
    },
    // 初始化
    mounted(){
        this.gtPost();

        
        uni.$on("loginSuccess", ()=>{
            this.requestCount = 0;
            this.requestParam.pageNumber = 1;
            this.postData.content = [];
            this.isRefresherTriggered = false;
            this.isEnd = false;
            this.isPushing = false;
            this.gtPost();
        });
    },
    methods:{
        gtPost(){
			if(this.isPushing||this.isEnd){
				return false;
			}

			this.isPushing = true;
			this.$kt.request.request({
				api: this.$api.business.ovo.post.getNear,
				data: this.requestParam,
				stateSuccess: res => {
					var content = res.data.content;
					if(content.length==0){
						this.isEnd = true;
						this.isPushing = false;
						setTimeout(()=>{
							this.isEnd = false;
						},30000);
						return false;
					}
					this.requestParam.pageNumber++;
					for (var i = 0; i < content.length; i++) {
                        this.$ovo.setPost(content[i]);
						this.postData.content.push(content[i]);
					}
					setTimeout(()=>{
						this.isPushing = false;
					},2000);
				}
			});
		}
    }
}
</script>

<style lang="scss" scoped>


.hot-post {
    background-color: #fff;
    box-shadow: 0 0 10rpx #eee;
    overflow: hidden;
    padding: 0 20rpx 20rpx 20rpx;
    box-sizing: border-box;

    .hot-post-title-icon {
        width: 50rpx;
        height: 50rpx;
        margin-right: 10rpx;
        vertical-align: middle;
        vertical-align: bottom;
    }

    .hot-post-title {
        padding: 10rpx;
        font-size: 36rpx;
        font-weight: bold;
        vertical-align: bottom;
    }

}

.post-list {
    background-color: #fff;
    overflow: hidden;
    padding: 0 20rpx 0rpx 20rpx;
    box-sizing: border-box;

    .post-list-content {
        font-size: 36rpx;
        vertical-align: bottom;
    }

    .post-list-content-item {
        box-sizing: border-box;
        .post-list-content-item-title {
            font-size: 32rpx;
            font-weight: bold;
            margin-bottom: 20rpx;
        }
        .post-list-content-item-content {
            font-size: 30rpx;
            color: #333333;
            margin-bottom: 20rpx;
            // 文字间距
            letter-spacing: 2rpx;
            margin-bottom: 20rpx;
            margin-top: 20rpx;
        }
    }

}
.post-list-content-item-bottom-1:active{
    opacity: 0.7;
}

.post-list-content-item-bottom {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 0rpx;
    margin-bottom: 20rpx;
    .post-list-content-item-bottom-item {
        display: flex;
        align-items: center;
        margin-left: 30rpx;
        font-size: 24rpx;
        color: #333333;
        .post-list-content-item-bottom-item-icon {
            width: 35rpx;
            height: 35rpx;
            margin-right: 10rpx;
        }
    }
}

.post-list-content-item-bottom-item:active{
    opacity: 0.5;
}

</style>